/* react-animated slider */

.slider-wrapper {
  position: relative;
  height: 85vh;
  overflow: hidden;
  width: 99.9%;
  margin-top: 10%;
  border-radius: 50px;
  background: linear-gradient(145deg, rgb(255, 129, 61, 0.7), rgb(240, 129, 61, 0.9));
  box-shadow: -20px -20px 60px rgb(245, 90, 7), -20px -20px 60px rgb(236, 99, 57);
}

.slide {
  height: 70vh;
  background-size: cover !important;
}

.slide::before {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(90, 60, 30, 0.9)));
  /* background: linear-gradient(transparent, rgba(0, 0, 0, 0.9)); */
  bottom: 0;
  left: 0;
}

.previousButton, .nextButton {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 10;
  background: url(/static/media/right-arrow.57d77b4f.png) no-repeat center center / 34px;
  width: 128px;
  height: 128px;
  text-indent: -9999px;
  cursor: pointer;
}

.previousButton:hover, .nextButton:hover {
  background: url(/static/media/right-arrow.57d77b4f.png) no-repeat center center / 34px;
}

.previousButton {
  left: -10.0%;
  -webkit-transform: rotate(180deg) translateY(calc(50% + 0px));
  transform: rotate(180deg) translateY(calc(50% + 0px));
}

.previousButton:hover {
  left: -25px;
}

.nextButton {
  right: -9.5%;
}

.nextButton:hover {
  right: -25px;
}

.slider-content {
  text-align: center;
}

.slider-content .inner {
  padding: 0 70px;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  top: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.slider-content .inner button {
  transition-delay: 0s;
  margin-top: 10%;
}

