	.glowing {
		content: '';
		/* position: absolute;
		top:10px;
		left:0px;
		right:5px;
		bottom: 5px; */
		display: inline-block;
		min-width: 370px;
		max-width: 385px;
		height: 60px;
		text-align: center;
		line-height: 60px;
		color: #fff;
		font-size: 24px;
		text-transform: uppercase;
		text-decoration: none;
		font-family: sans-serif;
		box-sizing: border-box;
		background: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
		background-size: 400%;
		border-radius: 40px;
		z-index: 10010;
		opacity: 1;
		position: relative;
	}

	.glowing:hover {
		animation: animate 9s linear infinite;
		opacity: 1;
	}

	.glowing:before {
		content: '';
		/* position: absolute;
		   top:0px;
		   left:0px;
		   right:5px;
		   bottom: 5px; */
		z-index: -1;
		min-width: 400px;
		max-width: 450px;
		height: 60px;
		background: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
		background-size: 400%;
		border-radius: 40px;
		transition: 0.5s;
		animation: animate 9s linear infinite;
		opacity: 1;
	}

	@keyframes animate {
		0% {
			background-position: 0%;
		}

		100% {
			background-position: 400%;
		}
	}

