	.liquid {
		position: relative;
		left: -15px;
		top: -4px;
		width: 160px;
		height: 140px;
		background-color: #4973ff;
		box-shadow: inset 0 0 10px rgba(255, 0, 0, 0.8);
		transition: 0.5s;
