	#plants {
		transform-origin: center center;
		transform-box: fill-box;
		transform: perspective(500px);
		animation: rotateReverse 3s infinite 0s linear alternate;
	}

	@keyframes rotateReverse {
		0% {
			transform: scaleX(30px);
			transform: skewX(20deg);
		}

		50% {
			transform: scaleX(-10px);
			transform: skewX(12deg);
		}

		100% {
			transform: scaleX(30px);
			transform: skewX(5deg);
		}
	}

	#Manager {
		transform-origin: top left;
		transform-box: fill-box;
		transform: perspective(200px);
		animation: zooomMgr 5s infinite 0s linear alternate;
	}

	@keyframes zooomMgr {
		0% {
			transform: skewX(1deg) rotateY(-0deg);
		}

		50% {
			transform: skewX(5deg) rotateY(-10deg);
		}

		100% {
			transform: skewX(5deg) rotateY(-40deg);
		}
	}

